
import {useState, useEffect, createContext} from 'react';
import firebase from '../services/firebaseConnection';
import { toast } from 'react-toastify';

export const AuthContext = createContext({});

function AuthProvider({children}){

    const [user, setUser] = useState(null) ;
    const [loadingAuth, setLoadingAuth] = useState(false);
    const [loading, setLoading] = useState(true);

  

    useEffect(() =>{
   
        
        function loadStorage() {
            const storageUser = localStorage.getItem('SistemaUser');

            if(storageUser){
                setUser(JSON.parse(storageUser));
                setLoading(false);
            }

            setLoading(false);
        }

        loadStorage()

    }, [])

    //Cadastrando um novo usuário
    async function signUp(email, password, nome){
        setLoadingAuth(true);

        await firebase.auth().createUserWithEmailAndPassword(email, password)
        .then(async (value) => {

            let uid = value.user.uid;

            await firebase.firestore().collection('users')
            .doc(uid).set({
                nome: nome,
                avatarUrl: null,
            })
            .then(() => {

                let data = {
                    uid: uid,
                    nome: nome,
                    email: value.user.email,
                    avatarUrl: null
                }

                setUser(data);
                storageUser(data);
                setLoadingAuth(false);
                toast.success('Bem vindo a plataforma');
            })
        })
        .catch((error) => {
            console.log(error)
            toast.error('Ops, algo deu errado!');
            setLoadingAuth(false);
           

        })
       
    }

    
    //Fazer o login de um novo usuário
    async function signIn(email, password){
        
        setLoadingAuth(true);

        await firebase.auth().signInWithEmailAndPassword(email, password)
        .then(async (value)=>{

          let uid  = value.user.uid

          const userProfile = await firebase.firestore().collection('users')
          .doc(uid).get();

          let data = {
            uid: uid,
            nome: userProfile.data().nome,
            avatarUrl: userProfile.data().avatarUrl,
            email: value.user.email
          };

          setUser(data);
          storageUser(data);
          setLoadingAuth(false);
          toast.success(`Bem vindo de volta, ${data.nome}!`)


        })
        .catch((error)=>{
          console.log(error);
          setLoadingAuth(false);
          toast.error('Ops, algo deu errado!')
        })
      };
    

    //Fazer logout do usuário
    async function signOut(){
        await firebase.auth().signOut();
        localStorage.removeItem('SistemaUser');
        setUser(null);
    }

    //Salvar usuário no localstorage
    function storageUser(data){
        localStorage.setItem('SistemaUser', JSON.stringify(data));
    }




    return(
        <AuthContext.Provider 
        value={{
            signed: !!user,
            user,
            loading, 
            signUp,
            signOut,
            signIn,
            loadingAuth,
            storageUser,
            setUser

        }}
        >
            {/* 
            propriedade "signed" está sendo usada no route e ela recebe a state de user, para fazer a autenticação e realizar o login

            !!user => a duas exclamações estão convertendo o conteudo da state user em booleano, logo se tiver algo dentro do user, ela returna um true agora se não tiver, ele retorna um false  */}

            {children}
        </AuthContext.Provider>
    );
}

export default AuthProvider;

